@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=JetBrains+Mono:wght@400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Prism.js Light Theme */
pre[class*="language-"] {
  color: #333;
  background: #f8f9fa;
  font-family: 'JetBrains Mono', Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  font-size: 0.9em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.6;
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;

  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;

  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

/* Dark Theme for Code Blocks */
[data-theme="dark"] pre[class*="language-"] {
  color: #e6edf3;
  background: #0d1117;
  border: 1px solid #30363d;
}

code[class*="language-"] {
  background: transparent;
}

/* Light Theme Tokens */
.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #6a737d;
  font-style: italic;
}

.token.punctuation {
  color: #586069;
}

.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
  color: #d73a49;
}

.token.function-name {
  color: #6f42c1;
}

.token.boolean,
.token.number {
  color: #005cc5;
}

.token.function {
  color: #6f42c1;
}

.token.property,
.token.class-name,
.token.constant,
.token.symbol {
  color: #e36209;
}

.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
  color: #d73a49;
  font-weight: 600;
}

.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
  color: #032f62;
}

.token.operator,
.token.entity,
.token.url {
  color: #d73a49;
}

/* Dark Theme Tokens */
[data-theme="dark"] .token.comment,
[data-theme="dark"] .token.block-comment,
[data-theme="dark"] .token.prolog,
[data-theme="dark"] .token.doctype,
[data-theme="dark"] .token.cdata {
  color: #8b949e;
  font-style: italic;
}

[data-theme="dark"] .token.punctuation {
  color: #c9d1d9;
}

[data-theme="dark"] .token.tag,
[data-theme="dark"] .token.attr-name,
[data-theme="dark"] .token.namespace,
[data-theme="dark"] .token.deleted {
  color: #ffa7c4;
}

[data-theme="dark"] .token.function-name {
  color: #d2a8ff;
}

[data-theme="dark"] .token.boolean,
[data-theme="dark"] .token.number {
  color: #79c0ff;
}

[data-theme="dark"] .token.function {
  color: #d2a8ff;
}

[data-theme="dark"] .token.property,
[data-theme="dark"] .token.class-name,
[data-theme="dark"] .token.constant,
[data-theme="dark"] .token.symbol {
  color: #ffa657;
}

[data-theme="dark"] .token.selector,
[data-theme="dark"] .token.important,
[data-theme="dark"] .token.atrule,
[data-theme="dark"] .token.keyword,
[data-theme="dark"] .token.builtin {
  color: #ff7b72;
  font-weight: 600;
}

[data-theme="dark"] .token.string,
[data-theme="dark"] .token.char,
[data-theme="dark"] .token.attr-value,
[data-theme="dark"] .token.regex,
[data-theme="dark"] .token.variable {
  color: #a5d6ff;
}

[data-theme="dark"] .token.operator,
[data-theme="dark"] .token.entity,
[data-theme="dark"] .token.url {
  color: #ff7b72;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

/* Custom CSS Variables for Anthropic-inspired theme */
:root {
  --bg-primary: #fdfcfa;
  --bg-warm: #f7f5f3;
  --bg-chocolate: #8b6f47;
  --text-primary: #2d2926;
  --text-secondary: #6b6459;
  --accent-warm: #c4956c;
  --accent-gold: #d4a574;
  --shadow-warm: 0 4px 20px rgba(139, 111, 71, 0.08);
}

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-warm) 100%);
  color: var(--text-primary);
  min-height: 100vh;
}

code {
  font-family: 'JetBrains Mono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Enhanced typography */
.font-display {
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  letter-spacing: -0.025em;
}

/* Warm shadows and cards */
.shadow-warm {
  box-shadow: var(--shadow-warm);
}

.bg-warm {
  background-color: var(--bg-warm);
}

.text-warm {
  color: var(--text-secondary);
}

/* Chocolate accent elements */
.accent-chocolate {
  background-color: var(--bg-chocolate);
}

.border-warm {
  border-color: var(--accent-warm);
}

/* Custom button styles */
.btn-warm {
  background: linear-gradient(135deg, var(--accent-warm) 0%, var(--accent-gold) 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-warm);
}

.btn-warm:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 25px rgba(139, 111, 71, 0.15);
}

/* Enhanced code blocks */
pre[class*="language-"] {
  background: #f8f9fa !important;
  border: 1px solid #e2e8f0;
  box-shadow: var(--shadow-warm);
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}
